@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300..700&display=swap');

:root {
  --bg: white;
  --text: black;
  --button-bg: #dedede;
  --button-bg-hover: #cdcdcd;
}
.dark-theme {
  --bg: #1c1c1c;
  --text: white;
  --button-bg: #2c2c2c;
  --button-bg-hover: #3c3c3c;
}

body {
  background-color: var(--bg);
  margin: 0;
  font-family: "Quicksand", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.maze-container {
  justify-content: center;
  align-items: center;
  display: flex;
}
.maze {
  display: grid;
  grid-template-columns: repeat(10, 3.5vw);
  border-left: 3px solid var(--text);
  border-bottom: 3px solid var(--text);
  width: fit-content;
}
.square {
    display: flex;
    width: 3.5vw;
    font-size: 2rem;
    aspect-ratio: 0.9;
    border-color: var(--text);
    box-sizing: border-box;
    justify-content: center;
    align-items: center;
    text-align: center;
}
.share-code-container {
  margin-top: 5px;
  display: grid;
}
.share-code-input {
  justify-self: center;
  text-align:center;
  font-size: 1.2rem;
  font-weight: bold;
  width: 60.75vw;
  height: 1.7rem;
  resize: none;
  background-color: var(--button-bg);
  color: var(--text);
  border: none;
}
.copy-share-code {
  justify-self: center;
  border-radius: 5%;
  width: fit-content;
  margin-top: 0.3%;
}
.timer-container {
  display: flex;
  justify-content: center;
}
.timer {
  display: flex;
  align-items:end;
  gap: 12vw;
  margin-bottom: 5px;
  margin-top: 20px;
  font-size: 2rem;
  font-weight: bold;
  color: var(--text);
}

.mobile-controls {
  display: none;
}

button {
  border-radius: 15%;
  padding: 8px;
  font-size: 18px;
  font-weight: bold;
  color: var(--text);
  background-color: var(--button-bg);
  border: none;
}
button:hover {background-color: var(--button-bg-hover);}
button:active {
  transform: translateY(1px);
}
.theme-toggle {
  right: 1%;
  top: 1%;
  position: absolute;
  font-size: 150%;
  padding: none;
}

@media only screen and (max-width: 1000px) { 
    .timer {
      gap: 5rem;
    }
    .theme-toggle {
      right: 1%;
      top: 94%;
      position: absolute;
      font-size: 150%;
      padding: none;
    }
    .share-code-input {
      height: 7rem;
    }
    .maze {
      grid-template-columns: repeat(10, 9vw);
    }
    .square {
      width: 9.1vw;
    }
    .mobile-controls {
      display:grid;
      grid-template-areas:
        ". up ."
        "left . right"
        ". down .";
      grid-template-columns: 7vh 7vh 7vh;
      grid-template-rows: 7vh 7vh 7vh;
      gap: 1vh;
      justify-content: center;
      margin: 1vw;
      button {
        font-size: 2rem;
      }
    }
    .mobile-controls button:nth-child(1) { grid-area: up; }
    .mobile-controls button:nth-child(2) { grid-area: left; }
    .mobile-controls button:nth-child(3) { grid-area: down; }
    .mobile-controls button:nth-child(4) { grid-area: right; }
}

